.contactContainer h1{
    font-family: "Metrophobic";
    font-size: 28px;
    color: #2C353D;
    text-align: center;
}

.contactGrid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2em 3em 6em 3em;
    column-gap: 3em;
}

.formWrapper{
    display: grid;
    grid-template-rows: repeat(4, auto);
    row-gap: 1em;
}

.formWrapper input{
    border: rgb(231, 234, 238) 1px solid;
    padding: 1.2em 1.8em;
    border-radius: 3px;
    color: #90a1ae;
    font-weight: 400;
    font-size: 0.875rem;
}

.formWrapper textarea{
    border: rgb(231, 234, 238) 1px solid;
    padding: 1.8em 1.8em;
    border-radius: 3px;
    color: #90a1ae;
    line-height: 24px;
    font-weight: 400;
    font-size: 0.875rem;
    min-height: 200px;
}

.formWrapper button{
    padding: 1em 2.2em;
    background-color: #efbb20;
    border: none;
    border-radius: 3px;
    font-size: 14px;
    color: rgb(47, 53, 60);
}

.infoWrapper h2{
    margin: 0 0 1em 0;
    padding: 0;
    font-family: 'Metrophobic';
}

.infoWrapper h3{
    font-family: 'IBM Plex Sans';
    font-weight: 500;
    font-size: 15px;
    margin-top: 1em;
    margin-bottom: 0.5em;
}

@media only screen and (max-width: 800px) {
    .contactGrid{
        grid-template-columns: 1fr;
    }
    .infoWrapper{
        display: grid;
        grid-template-columns: repeat (3,1fr );
    }
}

@media only screen and (max-width: 600px) {
    .contactGrid{
        display: grid;
        grid-template-columns: 1fr;
        padding: 2em 1.5em 5em 1.5em;
        row-gap: 3em;
    }
}