.container{
    padding: 4.5em 3em;
}

.sectionTitle{
    font-family: "Metrophobic";
    font-size: 28px;
    color: #2C353D;
    text-align: center;
}

.dividerLine{
    height: 2px;
    background-color: #efbb20;
    width: 60px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1em;
    margin-bottom: 3.5em;
}

.servicesGrid{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
    column-gap: 4em;
    row-gap: 4em;
}

@media only screen and (max-width: 600px) {
    .container{
        padding: 4.5em 1.5em;
    }
    .servicesGrid{
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
}