.worksGrid{
    width: 100%;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
    column-gap: 1em;
    row-gap: 1em;
}

@media only screen and (max-width: 600px) {
    .worksGrid{
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
}