.workBoxContainer{
    width: 100%;
    height: 300px;
    opacity: 0.7;
    transition: 0.5s ease;
    overflow: hidden;
    position: relative;
}

.workBoxContainer:hover{
    opacity: 1;
}

.workBoxContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 0.5s ease;
}

.overlay{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgb(248, 248, 248);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
    z-index: 4;
}

.workBoxContainer:hover .overlay{
    height: 20%;
}

.overlayText{
    font-family: 'Metrophobic';
    font-size: 15px;
    color: rgb(34, 34, 34);
    position: absolute;
    top: 50%;
    left: 20%;
    transform: translate(-50%,-50%);
}

.workBoxContainer:hover img{
    scale: 1.1;
}