.aboutContainer{
    background-color: rgb(47, 53, 60);
    background-image: url('/public/images/aboutBG.jpg');
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding: 6em 10em;
    column-gap: 3em;
    box-shadow: 0px 10px 30px -17px rgba(0,0,0,0.82);
    -webkit-box-shadow: 0px 10px 30px -17px rgba(0,0,0,0.82);
    -moz-box-shadow: 0px 10px 30px -17px rgba(0,0,0,0.82);
}

.aboutLeft{
    position: relative;
}

.logoAbout{
    position: absolute;
    top: 50%;
    left: 40%;
    transform: translate(-50%,-50%);
    font-size: 40px;
    color: #ffffff;
    margin: 0 !important;
    padding: 0 !important;
}

.aboutRight h2{
    font-family: "Metrophobic";
    font-size: 28px;
    color: #ffffff;
}

.dividerLineAbout{
    height: 2px;
    background-color: #efbb20;
    width: 60px;
    margin-top: 1em;
    margin-bottom: 2em;
}

.aboutRight p{
    color: rgb(228, 228, 228);
}

@media only screen and (max-width: 850px) {
    .aboutContainer{
        grid-template-columns: 1fr;
        padding: 6em 3em;
    }

    .logoAbout{
        left: 0%;
        transform: translate(0%,-50%);
    }
}

@media only screen and (max-width: 600px) {
    .aboutContainer{
        grid-template-columns: 1fr;
        padding: 2em 1.5em 6em 1.5em;
    }

    .logoAbout{
        left: 0%;
        transform: translate(0%,-50%);
    }

    .aboutLeft{
        display: none;
    }
}