.footerBottom{
    display: flex;
    justify-content: space-between;
    background-color: rgb(47, 53, 60);
    padding: 1em 3.1em;
}

.copy, .backToTop{
    font-family: 'IBM Plex Sans';
    color: #ffffff;
    font-size: 12px;
}

.backToTop{
    cursor: pointer;
    transition: 0.5s ease;
}

.backToTop:hover{
    color: #efbb20;
}

.footerBottom a{
    font-family: 'IBM Plex Sans';
    color: #ffffff;
    font-size: 12px;
    text-decoration: none;
    transition: 0.4s ease;
}

.footerBottom a:hover{
    color: #efbb20;
}

@media only screen and (max-width: 600px) {
    .footerBottom{
        padding: 1em 1.5em;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }
    .backToTop{
        margin-top: 1em;
    }
}